import { FC, ChangeEvent, useState, useCallback } from 'react';
import numeral from 'numeral';
import PropTypes from 'prop-types';
import { api } from 'src/api/api';
import {AiOutlineOrderedList} from 'react-icons/ai';

import {
  Tooltip,
  Divider,
  Box,
  Card,
  Checkbox,
  IconButton,
  Table,
  TableBody,
  TableCell,
  TableHead,
  TablePagination,
  TableRow,
  TableContainer,
  Typography,
  useTheme,
  CardHeader
} from '@mui/material';

import { read, utils, writeFileXLSX } from 'xlsx';
import EditTwoToneIcon from '@mui/icons-material/EditTwoTone';
import BulkActions from './BulkActions';
import { FaCloudDownloadAlt } from 'react-icons/fa'
import { IProduct } from 'src/models/product';
import { ListProductsModal } from 'src/components/Modals/ListProductsModal/ListProductsModal';
import { ValuesProducts } from 'src/components/Modals/ValuesProducts/ValuesProducts';
import { useNavigate } from 'react-router';



interface ProductsTableProps {
  className?: string;
  products: IProduct[];
}


const applyFilters = (
  products: IProduct[],
): IProduct[] => {
  return products.filter((product) => {
    let matches = true;

    /*
    if (filters.status && productOrder.status !== filters.status) {
      matches = false;
    }*/

    return matches;
  });
};

const applyPagination = (
  products: IProduct[],
  page: number,
  limit: number
): IProduct[] => {
  return products.slice(page * limit, page * limit + limit);
};

const ProductsTable: FC<ProductsTableProps> = ({ products }) => {
  const [selectedProducts, setSelectedProducts] = useState<string[]>(
    []
  );
  const selectedBulkActions = selectedProducts.length > 0;
  const [page, setPage] = useState<number>(0);
  const [limit, setLimit] = useState<number>(5);

  const exportFile = useCallback(() => {
    const ws = utils.json_to_sheet(products);
    const wb = utils.book_new();
    utils.book_append_sheet(wb, ws, "Data");
    writeFileXLSX(wb, "Produtos.xlsx");
  }, [products]);

  const handleSelectAllProducts = (
    event: ChangeEvent<HTMLInputElement>
  ): void => {
    setSelectedProducts(
      event.target.checked
        ? products.map((product) => product.id)
        : []
    );
  };



  const handleSelectOneProduct = (
    event: ChangeEvent<HTMLInputElement>,
    product: string
  ): void => {
    if (!selectedProducts.includes(product)) {
      setSelectedProducts((prevSelected) => [
        ...prevSelected,
        product
      ]);
    } else {
      setSelectedProducts((prevSelected) =>
        prevSelected.filter((id) => id !== product)
      );
    }
  };

  const handlePageChange = (event: any, newPage: number): void => {
    setPage(newPage);
  };

  const handleLimitChange = (event: ChangeEvent<HTMLInputElement>): void => {
    setLimit(parseInt(event.target.value));
  };

  const filteredProducts = applyFilters(products);
  const paginatedProducts = applyPagination(
    filteredProducts,
    page,
    limit
  );
  const selectedSomeProducts =
    selectedProducts.length > 0 &&
    selectedProducts.length < products.length;
  const selectedAllProducts =
    selectedProducts.length === products.length;

  const theme = useTheme();

  const navigate = useNavigate()

  const [data, setData] = useState<IProduct[]>([]);
  const [valuesProduct, setValuesProduct] = useState(null);
  const [idProduct, setIdProduct] = useState<IProduct[]>([]);
  const [modal, setModal] = useState<Boolean>(false);
  const [modalValues, setModalValues] = useState<Boolean>(false)

  async function queryProductsById(idProduct: any){
    setModal(!modal)
    setData(null)
    const {data} = await api.get(`/produtos/${idProduct}`);
    if(data){
      setData(data);
    }else{
      setData(null);
    }
  }

  async function queryValuesById(idProduct: any){
    setModalValues(!modal)
    setValuesProduct(null)
    const {data} = await api.get(`/produtos/precos/${idProduct}`);
    if(data){
      setValuesProduct(data);
    }else{
      setValuesProduct(null);
    }
  }


  return (
    <Card>
      {selectedBulkActions && (
        <Box flex={1} p={2}>
          <BulkActions id_product={selectedProducts}/>
        </Box>
      )}
      {!selectedBulkActions && (
        <CardHeader
          action={
            <Box width={150}>

                  <FaCloudDownloadAlt onClick={exportFile} style={{
                  textAlign: 'right',
                  position: 'relative',
                  left: '7rem',
                  height: '25px',
                  width: '25px',
                  cursor: 'pointer'
               }}/>
            </Box>
          }
          title="Produtos cadastrados"
        />
      )}
      <Divider />
      <TableContainer>
        <Table>
          <TableHead>
            <TableRow>
              <TableCell>
                
              </TableCell>
              <TableCell>Produto</TableCell>
              <TableCell>Marca</TableCell>
              <TableCell>Categoria</TableCell>
              <TableCell align="right">Valor Diário</TableCell>
              <TableCell align="right">Ações</TableCell>
            </TableRow>
          </TableHead>
          <TableBody>
            {paginatedProducts.map((produto) => {
              const isProductSelected = selectedProducts.includes(
                produto.id
              );
              return (
                <TableRow
                  hover
                  key={produto.id}
                  selected={isProductSelected}
                >
                  <TableCell padding="checkbox">
                    <Checkbox
                      color="primary"
                      checked={isProductSelected}
                      onChange={(event: ChangeEvent<HTMLInputElement>) =>
                        handleSelectOneProduct(event, produto.id)
                      }
                      value={isProductSelected}
                    />
                  </TableCell>
                  <TableCell>
                    <Typography
                      variant="body1"
                      fontWeight="bold"
                      color="text.primary"
                      gutterBottom
                      noWrap
                    >
                      {produto.nome}
                    </Typography>
                  </TableCell>
                  <TableCell>
                    <Typography
                      variant="body1"
                      fontWeight="bold"
                      color="text.primary"
                      gutterBottom
                      noWrap
                    >
                      {produto.fabricante}
                    </Typography>
                  </TableCell>
                  <TableCell>
                    <Typography
                      variant="body1"
                      fontWeight="bold"
                      color="text.primary"
                      gutterBottom
                      noWrap
                    >
                      {produto?.categoria?.descricao}
                    </Typography>
                  </TableCell>
                  <TableCell align="right">
                    <Typography
                      variant="body1"
                      fontWeight="bold"
                      color="text.primary"
                      gutterBottom
                      noWrap
                    >

                    </Typography>
                    <Typography variant="body2" color="text.secondary" noWrap>
                      R${numeral(produto.valor).format(
                        `0,0.00`
                      )}
                    </Typography>
                  </TableCell>
                  <TableCell align="right">
                    <Tooltip title="Editar produto" arrow>
                      <IconButton
                        sx={{
                          '&:hover': {
                            background: theme.colors.primary.lighter
                          },
                          color: theme.palette.primary.main
                        }}
                        color="inherit"
                        size="small"
                        onClick={(e)=>queryProductsById(produto?.id)}
                      >
                        <EditTwoToneIcon fontSize="small" />
                      </IconButton>
                    </Tooltip>
                    <Tooltip title="Editar valores diários" arrow>
                      <IconButton
                        sx={{
                          '&:hover': {
                            background: theme.colors.primary.lighter
                          },
                          color: theme.palette.primary.main
                        }}
                        color="inherit"
                        size="small"
                        onClick={(e)=>queryValuesById(produto?.id)}
                      >
                        <AiOutlineOrderedList fontSize="medium" />
                      </IconButton>
                    </Tooltip>
                  </TableCell>
                </TableRow>
              );
            })}
          </TableBody>
        </Table>
      </TableContainer>
      <Box p={2}>
        <TablePagination
          component="div"
          count={filteredProducts.length}
          onPageChange={handlePageChange}
          onRowsPerPageChange={handleLimitChange}
          page={page}
          rowsPerPage={limit}
          rowsPerPageOptions={[5, 10, 25, 30]}
        />
      </Box>
      {data  && (
        <ListProductsModal 
          openModal={modal}
          setModal={setModal}
          data={data}
          edit={true}
      />
      )}

      {valuesProduct  &&(
        <ValuesProducts 
          openModal={modalValues}
          setModal={setModalValues}
          data={valuesProduct}
          edit={true}
      />
      )}
    </Card>
  );
};

ProductsTable.propTypes = {
  products: PropTypes.array.isRequired
};

ProductsTable.defaultProps = {
  products: []
};

export default ProductsTable;
