

import styles from '../Modal.module.scss';
import { AiFillCloseCircle } from 'react-icons/ai'
import { ModalPropsTestEdit } from 'src/default/interfaces/Interfaces';
import { useState, useContext, useEffect } from 'react';
import { Title } from 'src/components/Title/Title';
import { ICategory, IBrand } from 'src/models/product';
import { TextField, Select, InputLabel } from '@mui/material';
import { LoadingButton} from '@mui/lab';
import MenuItem from '@mui/material/MenuItem';
import http from 'src/http';


export const ListProductsModal = ({ openModal, setModal, data, edit }: ModalPropsTestEdit) => {

  const [nameProduct, setNameProduct] = useState<string>("");

  const [editedNameProduct, setEditedNameProduct] = useState<string>(data[0]?.nome);
  const [newNameProduct, setNewNameProduct] = useState<string>("");

  const [editedFabricProduct, setEditedFabricProduct] = useState<string>(data[0]?.fabricante);
  const [newFabricProduct, setNewFabricProduct] = useState<string>('');

  const [editedValueProduct, setEditedValueProduct] = useState<string>(data[0]?.valor);
  const [newValueProduct, setNewValueProduct] = useState<string>("");

  const [editedDescriptionProduct, setEditedDescriptionProduct] = useState<string>(data[0]?.descricao);
  const [newDescriptionProduct, setNewDescriptionProduct] = useState<string>("");

  const [editedDimensionProduct, setEditedDimensionProduct] = useState<string>(data[0]?.dimensao);
  const [newDimensionProduct, setNewDimensionProduct] = useState<string>("");

  const [editedHeightProduct, setEditedHeightProduct] = useState<string>(data[0]?.altura);
  const [newHeightProduct, setNewHeightProduct] = useState<string>("");

  const [editedTriggerProduct, setEditedTriggerProduct] = useState<string>(data[0]?.acionamento);
  const [newTriggerProduct, setNewTriggerProduct] = useState<string>("");

  const [editedCategoryProduct, setEditedCategoryProduct] = useState<string>(data[0]?.categoria?.id);
  const [newCategoryProduct, setNewCategoryProduct] = useState(null);

  const [editedImageProduct, setEditedImageProduct] = useState(data[0]?.prod_image);
  const [newImageProduct, setNewImageProduct] = useState(null);

  const [idProduct, setIdProduct] = useState<string>(data[0]?.id);

  const [loading, setLoading] = useState<Boolean>(false);
  const [message, setMessage] = useState<string>('');
  const [error, setError] = useState<string>('');

  const [categories, setCategories] = useState<ICategory[]>([]);
  const [brands, setBrands] = useState<IBrand[]>([]);

  const [file, setFile] = useState();
   const [file2, setFile2] = useState();

   const changeFile = (event) =>{
    setFile(event.target.files[0]);
  }


  const changeFile2 = (event) =>{
    setFile2(event.target.files[0]);
  }



  async function queryCategories() {
    setCategories(null)
    const { data } = await http.get(`/categorias`);
    if (data) {
      setCategories(data);
    } else {
      setCategories(null);
    }
  }

  async function queryBrands(){
    const {data} = await http.get("/marcas");
    setBrands(data);
  }

  

  //Criar produto 
  async function EditProductById(e: React.MouseEvent<HTMLButtonElement, MouseEvent>){
    if(editedNameProduct && editedValueProduct && editedFabricProduct && editedCategoryProduct && editedDimensionProduct
      && editedHeightProduct && editedTriggerProduct){
        e.preventDefault();
        setLoading(true);
        let formData = new FormData();
        formData.append("nome", editedNameProduct);
        formData.append("descricao", editedDescriptionProduct);
        formData.append("fabricante", editedFabricProduct);
        formData.append("valor", editedValueProduct);
        formData.append("id_categoria", editedCategoryProduct);
        formData.append("dimensao", editedDimensionProduct);
        formData.append("altura", editedHeightProduct);
        formData.append("acionamento", editedTriggerProduct);
        formData.append("prod_image", file2);
        const fetchProduct = fetch(`https://nestrental.com.br:80/api/produtos/${data[0].id}`, {
          method: 'PUT',
          body: formData, 
          redirect: 'follow'
        })

        const dataResp = await fetchProduct;
        const dataJson = await dataResp.json();
        if(dataJson){
          setMessage('Produto editado com sucesso.')
          setError('');
        }else{
          setError('Erro de servidor, tente novamente mais tarde.')
        }
        setLoading(false)
        setTimeout(()=>{
          window.location.reload();
        },1000)
        
    }else{
      setMessage('');
      setError('Por favor, preecha todos os campos.')
    }
  }


  //Criar produto 
  async function CreateProduct(e: React.MouseEvent<HTMLButtonElement, MouseEvent>){
    if(newNameProduct && newValueProduct && newFabricProduct && newCategoryProduct &&
      newDimensionProduct && newHeightProduct && newTriggerProduct && newDescriptionProduct && file
      ){
        e.preventDefault();
        setLoading(true);
        let formData = new FormData();
        formData.append("nome", newNameProduct);
        formData.append("descricao", newDescriptionProduct);
        formData.append("fabricante", newFabricProduct);
        formData.append("valor", newValueProduct);
        formData.append("id_categoria", newCategoryProduct);
        formData.append("dimensao", newDimensionProduct);
        formData.append("altura", newHeightProduct);
        formData.append("acionamento", newTriggerProduct);
        formData.append("prod_image", file);
        const fetchProduct = fetch(`https://nestrental.com.br:80/api/produtos`, {
          method: 'POST',
          body: formData, 
          redirect: 'follow'
        })

        const data = await fetchProduct;
        const dataJson = await data.json();
        if(dataJson){
          setMessage('Produto criado com sucesso.')
          setError('');
        }else{
          setError('Erro de servidor, tente novamente mais tarde.')
        }
        setLoading(false)
        window.location.reload();
    }else{
      setMessage('');
      setError('Preencha todos os campos obrigatórios.')
    }
  }

   useEffect(() => {
    queryCategories();
    queryBrands();
  }, [])
  
 


  return (
    <>
      {openModal && (
        <div className={styles.modal} style={{width: '800px'}}>
          <div className={styles.modal__divCloseButton}>
            <button
              className={styles.modal__divCloseButton__closeButton}
              onClick={(e) => setModal(!openModal)}
            >
              <AiFillCloseCircle />
            </button>
          </div>
          <section>
            <Title>
              {edit ? "Produto" : "Cadastre um novo produto"}
            </Title>
            <form>
              <div style={{marginTop: '1rem'}}>
                <TextField
                  required={true}
                  type="text"
                  name="name_product"
                  id="name_product"
                  placeholder="Digite o nome do produto"
                  color="primary"
                  fullWidth={true}
                  size="small"
                  label="Nome"
                  value={edit? editedNameProduct: newNameProduct}
                  onChange={
                    edit? (e)=>setEditedNameProduct(e.target.value): (e)=>setNewNameProduct(e.target.value)}
                />
                </div>

              <div style={{marginTop: '1rem'}}>
                <TextField 
                  required={true}
                  color="primary"
                  size="small"
                  type="number"
                  fullWidth={true}
                  name="value_product"
                  id="value_product"
                  placeholder="R$"
                  label="Valor R$"
                  value={edit? editedValueProduct: newValueProduct}
                  onChange={
                    edit? (e)=>setEditedValueProduct(e.target.value): (e)=>setNewValueProduct(e.target.value)}
                />
              </div>

              <div style={{display: 'flex', gridGap: '1rem', marginTop: '1rem'}}>
                <div style={{width: '100%'}}>
                  <TextField 
                    required={true}
                    fullWidth={true}
                    size="small"
                    color="primary"
                    type="text"
                    name="dimension_product"
                    id="dimension_product"
                    placeholder=""
                    label="Dimensão"
                    value={edit? editedDimensionProduct: newDimensionProduct}
                    onChange={
                    edit? (e)=>setEditedDimensionProduct(e.target.value): (e)=>setNewDimensionProduct(e.target.value)}
                  />
                </div>

                <div style={{width: '100%'}}>
                  <TextField 
                    type="text"
                    fullWidth={true}
                    required={true}
                    size="small"
                    color="primary"
                    label="Altura"
                    name="height_product"
                    id="height_product"
                    value={edit? editedHeightProduct: newHeightProduct}
                    onChange={
                    edit? (e)=>setEditedHeightProduct(e.target.value): (e)=>setNewHeightProduct(e.target.value)}
                  />
                </div>

                <div style={{width: '100%'}}>
                  <TextField 
                    type="text"
                    required={true}
                    fullWidth={true}
                    size="small"
                    color="primary"
                    label="Acionamento"
                    name="trigger_product"
                    id="trigger_product"
                    value={edit? editedTriggerProduct: newTriggerProduct}
                    onChange={
                      edit? (e)=>setEditedTriggerProduct(e.target.value): (e)=>setNewTriggerProduct(e.target.value)}
                  />
                </div>
              </div>

              <div style={{ display: 'flex', justifyContent: 'space-between', gridGap: '2rem', alignItems: 'center' }}>
                  <div style={{ width: '50%', marginTop: '1rem' }}>
                  <label style={{ display: 'block' }}>
                      Marca
                    </label>

                    {brands &&
                      <select
                        required={true}
                        onChange={edit? (e)=>setEditedFabricProduct(e.target.value): (e)=>setNewFabricProduct(e.target.value)}
                        value={edit? editedFabricProduct: newFabricProduct}
                        style={{borderRadius: '8px', border: '1px solid #c4c4c4', width: '100%', padding: '.8rem'}}
                      >
                         <option value="" style={{color: '#55627C', padding: '.5rem'}} selected>Selecione</option>
                        {brands.map((item, index) => {
                            return (
                              <option value={item.nome} key={item.id} style={{color: '#55627C', padding: '.8rem'}}>{item.nome}</option>
                            )
                        })}

                      </select>
                      }
                  </div>

                  <div style={{ width: '50%', marginTop: '1rem' }}>
                    <label style={{ display: 'block' }}>
                      Categoria
                    </label>

                    {categories &&
                      <select
                        defaultValue="Selecione:"
                        required={true}
                        onChange={edit? (e)=>setEditedCategoryProduct(e.target.value): (e)=>setNewCategoryProduct(e.target.value)}
                        value={edit? editedCategoryProduct: newCategoryProduct} 
                        style={{borderRadius: '8px', border: '1px solid #c4c4c4', width: '100%', padding: '.8rem'}}>
                        <option value="" selected>Selecione:</option>
                        {categories.map((item, index) => {
                          return <>
                            <option value={item.id} key={item.id} style={{color: '#55627C', padding: '.8rem'}}>{item.descricao}</option>
                          </>

                        })}
                      </select>
                    }
                  </div>
                
              </div>

              <div>

                

                <label style={{ display: 'block', marginTop: "1rem" }}>
                  Descrição do produto
                </label>
                <div style={{ display: 'flex', gridGap: '2rem', width: '100%' }}>
                  <textarea cols={40} rows={10} style={{ borderRadius: '8px', borderColor: '#ccc', padding: '1rem', resize: 'none', height: '200px'}} 
                    placeholder="Descreva o produto"  
                    required={true}
                    value={edit? editedDescriptionProduct : newDescriptionProduct} 
                    onChange={edit ? (e) => setEditedDescriptionProduct(e.target.value): (e)=>setNewDescriptionProduct(e.target.value)}>
                  </textarea>
                  {!edit ?
                    <div>
                       <label style={{ display: 'block', marginTop: '-1rem'}}>
                        Imagem do produto
                      </label>
                      <TextField
                        fullWidth={true}
                        color="primary"
                        type="file"
                        required={true}
                        onChange={(e)=>changeFile(e)}
                      />
                    </div> :
                    <div style={{width: '50%', marginTop: '-1.2rem'}}>
                      <label style={{ display: 'block'}}>
                        Atualizar Imagem
                      </label>
                      <TextField
                        fullWidth={true}
                        color="primary"
                        type="file"
                        required={true}
                        onChange={(e)=>changeFile2(e)}
                      />
                      <img 
                        src={`https://nestrental.com.br:80${data[0].prod_image}`}
                        alt=""
                        style={{ width: "18rem", height: "18rem", padding: '1rem', alignSelf: 'center', objectFit: 'contain' }} />
                    </div>
                  }

                </div>
              </div>

              
              <div style={{ display: 'flex', gridGap: '1rem', width: '20rem', marginTop: '1rem'}}>
                  {edit ? 
                    <LoadingButton    
                      type="submit"
                      color="primary"
                      variant="contained"
                      onClick={(e)=>EditProductById(e)}
                      loading={loading?true: false}
                    >
                      Editar
                    </LoadingButton>
                      :
                    <LoadingButton    
                      type="submit"
                      color="primary"
                      variant="contained"
                      onClick={(e)=>CreateProduct(e)}
                      loading={loading?true: false}
                    >
                      Criar
                    </LoadingButton>
                  }
                    <LoadingButton
                      type="submit"
                      color="warning"
                      variant="contained"
                      onClick={(e)=>setModal(!openModal)}
                    >
                      Cancelar
                    </LoadingButton>
                </div>
              
              {!loading && 
                message ? 
                  <p style={{color: 'green'}}>{message}</p>
                  :
                  <p style={{color: 'red'}}>{error}</p>
              }

            </form>
          </section>


        </div>
      )}
    </>
  )
}
