
import styles from '../Modal.module.scss';
import { ModalPropsTestEdit } from 'src/default/interfaces/Interfaces';
import React, { useEffect } from 'react';
import { UserContext } from 'src/context/UserContext';
import {AiFillCloseCircle, AiFillEdit} from 'react-icons/ai';
import { TextField } from '@mui/material';
import {LoadingButton} from '@mui/lab'

export const ValuesProducts = ({ openModal, setModal, data, edit }: ModalPropsTestEdit) => {

  const {
    usuario
  } = React.useContext(UserContext);

  const [message, setMessage] = React.useState('');
  const [dayValue, setDayValue] = React.useState('');
  const [day, setDay] = React.useState('');
  const [nameProduct, setNameProduct] = React.useState('');
  const [editProduct, setEditProduct] = React.useState(false);
  const [loading, setLoading] = React.useState(false);


  const EditValueProduct = async (e: any, index: any) =>{
    setModal(true)
    setLoading(true)
    e.preventDefault();
   const fetchProduct = fetch(`https://nestrental.com.br:80/api/produtos/precos/${data[0][0].id_produto}/${index}`,{
      method: 'PUT',
      headers:{
        'Content-type': 'application/json'
      }, body: JSON.stringify(({
        'dias': index,
        'valor': dayValue
      }))
    })

    const response = await fetchProduct;
    const json = await response.json();
    setMessage('Valor diário editado com sucesso.');
    setTimeout(()=>{
      setLoading(false);
    },3000)
   
    window.location.reload();
  }

  const getProductById = async () =>{
   const fetchProduct = fetch(`https://nestrental.com.br:80/api/produtos/${data[0][0].id_produto}`,{
      method: 'GET',
    })
    const response = await fetchProduct;
    const json = await response.json();
    setNameProduct(json[0].nome);
  }
  
  React.useEffect(()=>{
    getProductById();
  },[])

  return (
    <>
      {openModal && data ? (
        <div className={styles.modal} style={{width: '700px', padding: '1rem'}}>
          <div className={styles.modal__divCloseButton}>
            <button
              className={styles.modal__divCloseButton__closeButton}
              onClick={(e) => setModal(!openModal)}
            >
              <AiFillCloseCircle />
            </button>
          </div>

          <div style={{display: 'block', width: '100%', marginBottom: '1rem'}}>
            <div>
            <p style={{display: 'flex', alignItems: 'center',  gridGap: '10px',fontWeight: 'bold', fontSize: '1.4rem', marginLeft: '2.3rem', marginBottom: '-.5rem'}}>
              <AiFillEdit size={35}/> Tabela de Valores Diários - <label>{nameProduct}</label>
            </p>
              <ul style={{listStyle:'none', width: '100%'}}>
                <div style={{ display: 'flex', gridGap: '2rem', borderBottom: '1px solid #ccc'}}>
                  <p style={{fontWeight: 'bold', fontSize: '1rem', borderRight: '1px solid #ccc', paddingRight: '1rem'}}>Dias</p>
                  <p style={{fontWeight: 'bold', fontSize: '1rem', marginLeft: '-15px'}}>Valores</p>
                </div>
                {data ? data[0].map((item, index)=>{
                  return(
                    <div key={index} style={{marginTop: '1rem', display: 'flex', gridGap: '1rem', justifyContent: 'space-between', alignItems: 'center'}}>
                      <div style={{width: '10%', textAlign:'center'}}>
                        <p style={{borderBottom: '1px solid #ccc'}}>{index+1}</p>
                      </div>
                      <li key={index} style={{width: '100%'}}>
                        <TextField
                          color="primary"
                          type="text"  
                          id="product_value"
                          fullWidth={true}
                          required={true}
                          size='small'
                          onChange={(e)=>setDayValue(e.target.value)}
                          defaultValue={item.valor? item.valor : ''}
                        />
                      </li>
                      <LoadingButton
                        color="primary"
                        variant="contained"
                        size="large"
                        onClick={(e)=>EditValueProduct(e, index+1)}
                      >
                        Editar
                      </LoadingButton>
                    </div>
                   
                  )
                }): 
                
                <div style={{marginTop: '1rem', display: 'flex', gridGap: '1rem', justifyContent: 'space-between', alignItems: 'center'}}>
                      <div style={{width: '10%', textAlign:'center'}}>
                        <p style={{borderBottom: '1px solid #ccc'}}>{1}</p>
                      </div>
                      <li style={{width: '100%'}}>
                        <TextField
                          color="primary"
                          type="text"  
                          id="product_value"
                          fullWidth={true}
                          required={true}
                          size='small'
                          onChange={(e)=>setDayValue(e.target.value)}
                          
                        />
                      </li>
                      <LoadingButton
                        color="primary"
                        variant="contained"
                        size="large"
                        onClick={(e)=>EditValueProduct(e, 1)}
                      >
                        Editar
                      </LoadingButton>
                    </div>
                }
              </ul>
            </div>
          </div>

        </div>
      ) : ''}
    </>
  )
}
